import request from './request'

// 认证相关
export const login = (username, password) => {
  return request({
    url: '/auth/login',
    method: 'POST',
    data: { username, password }
  })
}

export const register = (data) => {
  return request({
    url: '/auth/register',
    method: 'POST',
    data
  })
}

export const getUserInfo = () => {
  return request({
    url: '/user/info',
    method: 'GET'
  })
}

// 课程相关
export const getCourses = (params) => {
  return request({
    url: '/courses',
    method: 'GET',
    data: params
  })
}

export const getCourseDetail = (courseId) => {
  return request({
    url: `/courses/${courseId}`,
    method: 'GET'
  })
}

// 预约相关
export const getReservations = () => {
  return request({
    url: '/reservations',
    method: 'GET'
  })
}

export const reserveCourse = (courseId) => {
  return request({
    url: '/reservations',
    method: 'POST',
    data: { course_id: courseId }
  })
}

export const cancelReservation = (reservationId) => {
  return request({
    url: `/reservations/${reservationId}`,
    method: 'DELETE'
  })
}

// 签到相关
export const getAttendance = (params) => {
  return request({
    url: '/attendance',
    method: 'GET',
    data: params
  })
}

// 会员卡相关
export const getCurrentMembership = () => {
  return request({
    url: '/memberships/current',
    method: 'GET'
  })
}

// 储物柜相关
export const getLockers = () => {
  return request({
    url: '/lockers',
    method: 'GET'
  })
}

